body {
  background-color: hsl(210, 46%, 95%);
  font-family: "Manrope", sans-serif;
  font-size: 1.3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  position: relative;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
}

img {
  max-width: 100%;
  display: block;
}

h1 {
  margin: 0;
  line-height: 1.5;
  font-size: 2rem;
}

p {
  line-height: 1.5;
}

.card {
  background-color: #FFFFFF;
  border-radius: 2rem;
  box-shadow: 5px 5px 20px -12px rgb(72, 85, 106);
}
@media (min-width: 768px) {
  .card {
    display: flex;
    align-items: center;
  }
}
.card__imagen {
  border-radius: 2rem 2rem 0 0;
  overflow: hidden;
}
@media (min-width: 768px) {
  .card__imagen {
    border-radius: 2rem 0 0 2rem;
  }
}
.card__info {
  padding: 4rem 3rem 2rem 3rem;
}
.card__heading {
  color: rgb(72, 85, 106);
}
.card__texto {
  color: hsl(214, 17%, 51%);
  font-weight: 600;
  margin-bottom: 0;
}
.card__contacto {
  background-color: rgb(255, 255, 255);
  padding: 2rem 3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 0 0 2rem 2rem;
}
.card__info-contacto {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.card__texto-share {
  text-transform: uppercase;
  letter-spacing: 0.6rem;
  color: hsl(212, 23%, 69%);
  font-size: 2rem;
  margin: 0;
}
.card__links {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.card__links img {
  width: 2.5rem;
  cursor: pointer;
}
@media (min-width: 768px) {
  .card__links img {
    width: 2rem;
  }
}
.card__avatar {
  width: 5rem;
  border-radius: 50%;
}
.card__nombre {
  margin: 0;
  color: rgb(72, 85, 106);
}
.card__fecha {
  margin: 0.5rem 0 0 0;
  color: hsl(212, 23%, 69%);
}
.card__enlace {
  position: relative;
  border-radius: 50%;
  padding: 1rem;
  background-color: hsl(210, 46%, 95%);
}
.card__share img {
  width: 2rem;
}
.card__flotante {
  position: absolute;
  top: -6rem;
  right: -8.8rem;
  background-color: rgb(72, 85, 106);
  width: 22rem;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  gap: 2rem;
  border-radius: 1rem;
}
.card__flotante::before {
  content: "";
  position: absolute;
  height: 1rem;
  width: 1rem;
  bottom: -2rem;
  right: 45%;
  transform: rotate(-90deg);
  border: 1rem solid transparent;
  border-right-color: rgb(72, 85, 106);
}
.card__flotante p {
  font-size: 1rem;
}

.footer {
  position: absolute;
  bottom: 0;
  margin-bottom: 2rem;
}
.footer a {
  color: hsl(214, 17%, 51%);
  text-decoration: none;
  font-weight: 700;
}